.dog_card__GEG6a {
  display: flex;
  padding: 2rem 2rem;
  position: relative;
  align-items: flex-end;
  border-color: transparent;
  border-radius: 20px;
  width: 280px;
  height: 320px;

  box-shadow: 5px 5px 30px 7px rgba(47, 132, 189, 0.397),
    -5px -5px 30px 7px rgba(74, 96, 131, 0.383);
  transition: 0.5s ease-in-out;
}

.dog_card__GEG6a:hover {
  box-shadow: 5px 5px 30px 7px rgba(85, 147, 247, 0.25),
    -5px -5px 30px 7px rgba(70, 113, 231, 0.22);
  cursor: pointer;
  -webkit-transform: translateY(15px);
          transform: translateY(15px);
}

.dog_card__GEG6a::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 20px;
  display: block;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    to bottom,
    rgba(61, 64, 65, 0.5),
    rgba(68, 149, 241, 0.239)
  );
  z-index: 2;
  transition: 0.5s all;
  opacity: 0;
}

.dog_card__GEG6a:hover:before {
  opacity: 1;
}
